<template>
  <div class="login">
    <div></div>
    <el-form :model="user" :rules="rules" ref="user" label-position="left"  label-width="60px" class="demo-ruleForm login-container">
      <h3 class="title">系统登录</h3>
      <el-form-item prop="account" label="账号">
        <el-input type="text" v-model="user.account" auto-complete="off" placeholder="请输入账号"></el-input>
      </el-form-item>
      <el-form-item prop="pwd" label="密码">
        <el-input type="password" v-model="user.pwd" auto-complete="off" placeholder="请输入密码"></el-input>
      </el-form-item>
      <el-checkbox v-model="checked" checked class="remember">记住密码</el-checkbox>
      <el-form-item style="margin-left: 40px">
        <el-button type="primary" @click.native.prevent="handleSubmit" :loading="logining">登录</el-button>
        <el-button type="danger" @click.native.prevent="handleReset">重置</el-button>
      </el-form-item>
    </el-form>
  </div>
</template>

<script>
  import { requestLogin } from '../api/api';
  export default {
    data() {
      return {
        logining: false,
        user: {
          account: '',
          pwd: ''
        },
        rules: {
          account: [
            { required: true, message: '请输入账号', trigger: 'blur' },
          ],
          pwd: [
            { required: true, message: '请输入密码', trigger: 'blur' },
          ]
        },
        checked: true
      };
    },
    methods: {
      handleReset() {
        this.$refs.user.resetFields();
      },
      handleSubmit(ev) {
        var _this = this;
        this.$refs.user.validate( (valid) => {
          if (valid) {
            this.logining = true;
            var loginParams = { username: this.user.account, password: this.user.pwd };
            requestLogin(loginParams).then(data => {
              this.logining = false;
              let { msg, code, user } = data;
              if (code !== 200) {
                this.$message({
                  message: msg,
                  type: 'error'
                });
              } else {
                sessionStorage.setItem('user', JSON.stringify(user));
                this.$router.push({ path: '/main' });
              }
            });
          }else{
            return false;
          }
        });
      }
    }
  }

</script>

<style  scoped>
  .login{
    overflow: hidden;
    background: url("../assets/login-bg.jpg") no-repeat;
  }
  .login-container {
    -webkit-border-radius: 5px;
    border-radius: 5px;
    -moz-border-radius: 5px;
    background-clip: padding-box;
    margin: 180px auto;
    width: 350px;
    padding: 35px 35px 15px 35px;
    background: #ececec;
    border: 1px solid #eaeaea;
    box-shadow: 0 0 25px #cac6c6;
  }
  .title {
    margin: 0px auto 40px auto;
    text-align: center;
    color: #505458;
  }
  .remember {
    margin: 0px 0px 35px 0px;
  }
</style>



